import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue" //导航栏
import vanField from '@/wxcomponents/vant/dist/field' //输入框
import vanGoodsAction from '@/wxcomponents/vant/dist/goods-action'
import vanGoodsActionButton from '@/wxcomponents/vant/dist/goods-action-button'
export default {
	components: {
		uniNavBar,
		vanField,
		vanGoodsAction,
		vanGoodsActionButton
	},
	data() {
		return {
			nav: {
				top: 0,
				height: 0
			},
			is_open_socket: false,
			// 手机号
			phone: '',
			// 用户名
			nickName: '',
			// 金额
			total: 0,
			// 数量
			amount: 0,
			// 确认订单的数据
			order: [],
			// 备注
			remark: ''
		}
	},
	created() {
		// 获取胶囊信息
		const nav = uni.getMenuButtonBoundingClientRect()
		this.nav.top = nav.top
		this.nav.height = nav.height
	},
	mounted() {
		this.Init(),
		this.getUserAndPhone()
	},
	methods: {
		// 将小程序缓存中的菜品数量重置为0
		resetQuality() {
			var arr = uni.getStorageSync('recommend')
			if (arr.length > 0) {
				for (var i in arr) {
					arr[i].quality = 0

				}
				uni.setStorageSync('recommend', arr)
			}
		},
		// 从小程序缓存中获取用户名和手机号
		getUserAndPhone() {
			var token = uni.getStorageSync('token')
			var phone = uni.getStorageSync('phone')
			this.nickName = token.nickName
			this.phone = phone
		},
		Init() {
			var arr = uni.getStorageSync('recommend')
			if (arr.length > 0) {
				for (var i in arr) {
					if (arr[i].quality > 0) {
						this.order.push(arr[i])
						this.total += arr[i].price * arr[i].quality
					}
				}
			}
			console.log(this.order)
			this.amount = this.order.length
		},

		// 稍后支付
		later() {
			var token = uni.getStorageSync('token')
			var phone = uni.getStorageSync('phone')
			if (!token) {
				uni.showToast({
					title: '请先授权登录',
					duration: 2000,
					icon: 'none',
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '../me/me'
					})
				}, 2000)
				return;
			}
			if (!phone) {
				uni.showToast({
					title: '请完善个人信息',
					duration: 2000,
					icon: 'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '../me/me'
					})
				}, 2000)
				return;
			}
			var data = this.getCacheData()
			var tableNum = uni.getStorageSync('tableNum')
			uni.request({
				url: 'https://www.linhua.club/ordering/wx/addOrder/later',
				method: "POST",
				data: {
					order: data,
					tableNum: tableNum,
					total: this.total,
					name: this.nickName,
					phone: this.phone
				},
				success: (res) => {
					console.log(res.data)
				}
			})
			this.resetQuality()
			uni.navigateTo({
				url: "../evaluate/evaluate"
			})

		},
		// 马上支付
		atOnce() {
			this.$goEasy.publish({
			    channel: 'my_channel',
			    message: '有新订单到~~'
			})
			var token = uni.getStorageSync('token')
			var phone = uni.getStorageSync('phone')
			if (!token) {
				uni.showToast({
					title: '请先授权登录',
					duration: 2000,
					icon: 'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '../me/me'
					})
				}, 2000)
				return;
			}
			if (!phone) {
				uni.showToast({
					title: '请完善个人信息',
					duration: 2000,
					icon: 'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '../me/me'
					})
				}, 2000)
				return;
			}
			var data = this.getCacheData()
			var tableNum = uni.getStorageSync('tableNum')
			uni.request({
				url: 'https://www.linhua.club/ordering/wx/addOrder/atOnce',
				method: "POST",
				data: {
					order: data,
					tableNum: tableNum,
					total: this.total,
					name: this.nickName,
					phone: this.phone
				},
				success: (res) => {
					console.log(res.data)
				}
			})
			this.resetQuality()
			uni.navigateTo({
				url: "../evaluate/evaluate"
			}) 
		},
		// 获取recommend缓存中的数据
		getCacheData() {
			var data = []
			var arr = uni.getStorageSync('recommend')
			if (arr.length > 0) {
				for (var i in arr) {
					if (arr[i].quality > 0) {
						data.push(arr[i])
					}
				}
			}
			return data
		},
		goback() {
			uni.navigateBack()
		}
	}
}
